/* open-sans-regular - cyrillic_cyrillic-ext_latin */
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/open-sans-v16-cyrillic_cyrillic-ext_latin-regular.eot"); /* IE9 Compat Modes */
  src: local("Open Sans Regular"), local("OpenSans-Regular"),
    url("../fonts/open-sans-v16-cyrillic_cyrillic-ext_latin-regular.eot?#iefix")
      format("embedded-opentype"),
    /* IE6-IE8 */
      url("../fonts/open-sans-v16-cyrillic_cyrillic-ext_latin-regular.woff2")
      format("woff2"),
    /* Super Modern Browsers */
      url("../fonts/open-sans-v16-cyrillic_cyrillic-ext_latin-regular.woff")
      format("woff"),
    /* Modern Browsers */
      url("../fonts/open-sans-v16-cyrillic_cyrillic-ext_latin-regular.ttf")
      format("truetype"),
    /* Safari, Android, iOS */
      url("../fonts/open-sans-v16-cyrillic_cyrillic-ext_latin-regular.svg#OpenSans")
      format("svg"); /* Legacy iOS */
}
